import { SizeUnit } from './type';
/**
 * [*] 
 * [x] 
 *
 * [x] bit                    b     0 or 1
 * [*] byte                 B     8 bits
 * [x] kilobit              kb    1000 bites
 * [*] kilobyte ()   KB    1024 bytes
 * [x] kilobyte ()   KB    1000 bytes
 * [x] Megabite           Mb    1000 kilobits
 * [*] Megabyte ()   KB    1024 kilobytes
 * [*] Megabyte ()   KB    1000 kilobytes
 * [x] Gigabit            Gb    1000 Megabite
 * [*] Gigabyte ()   GB    1024 Megabytes
 * [x] Gigabyte ()   GB    1000 Megabytes
 */

//  KB 
const SIZE_MAP = {
  B: 1024,
  KB: 1,
  MB: 1048576, // 1024 * 1024
  GB: 1073741824, // 1024 * 1024 * 1024
};

/**
 * 
 * @param size 
 * @param unit 
 */
export function isOverSizeLimit(fileSize: number, sizeLimit: number, unit: SizeUnit) {
  //  KB 
  const units = ['B', 'KB', 'MB', 'GB'];
  const KB_INDEX = 1;
  let index = units.indexOf(unit);
  if (index === -1) {
    console.warn(`TDesign Upload Warn: \`sizeLimit.unit\` can only be one of ${units.join()}`);
    index = KB_INDEX;
  }
  const num = SIZE_MAP[unit];
  const limit = index < KB_INDEX ? sizeLimit / num : sizeLimit * num;
  return fileSize <= limit;
}
