/**
 *  PMC
 * */

import { TdCountDownProps } from './type';
import { PropType } from 'vue';

export default {
  /**  */
  autoStart: {
    type: Boolean,
    default: true,
  },
  /** 'default' */
  content: {
    type: [String, Function] as PropType<TdCountDownProps['content']>,
    default: 'default',
  },
  /** DD-HH-mm-ss-SSS- */
  format: {
    type: String,
    default: 'HH:mm:ss',
  },
  /**  */
  millisecond: Boolean,
  /**  */
  time: {
    type: Number,
    required: true,
  },
  /**  */
  size: {
    type: String,
    default: 'small',
  },
  /**  */
  theme: {
    type: String,
    default: 'default',
  },
  /**  */
  onChange: Function as PropType<TdCountDownProps['onChange']>,
  /**  */
  onFinish: Function as PropType<TdCountDownProps['onFinish']>,
};
