/* eslint-disable */

/**
 *  PMC
 * */

import { TdRateProps } from './type';
import { PropType } from 'vue';

export default {
  /**  */
  allowHalf: Boolean,
  /**  */
  clearable: Boolean,
  /**  #ED7B2F   ['#ED7B2F', '#999999'] */
  color: {
    type: [String, Array] as PropType<TdRateProps['color']>,
  },
  /**  */
  count: {
    type: Number,
    default: 5,
  },
  /**  */
  disabled: Boolean,
  /**  */
  gap: {
    type: Number,
    default: 6,
  },
  /**  */
  showText: Boolean,
  /** `20` */
  size: {
    type: String,
    default: '',
  },
  /** ['', '', '', '', '']['1', '2', '3', '4', '5'] */
  texts: {
    type: Array as PropType<TdRateProps['texts']>,
  },
  /**  */
  value: {
    type: Number,
    default: undefined,
  },
  modelValue: {
    type: Number,
    default: undefined,
  },
  /**  */
  defaultValue: {
    type: Number,
    default: undefined,
  },
  /**  */
  variant: {
    type: String as PropType<TdRateProps['variant']>,
    default: 'filled' as TdRateProps['variant'],
    validator(val: TdRateProps['variant']): boolean {
      return ['outline', 'filled'].includes(val!);
    },
  },
  /**  */
  onChange: Function as PropType<TdRateProps['onChange']>,
};
