/**
 * @description: 
 * @return  | 
 */
export function findIndex(v: unknown, set: unknown): number {
  // 0='0'
  const reg = new RegExp(`^${!v && v !== 0 ? '' : v}$`);

  // 
  const arr = toArray(set);
  return arr.findIndex((s) => reg.test(s));
}

/**
 * @description:  undefined|null|''
 */
export function isFalsy(v: unknown): boolean {
  return !v && v !== 0;
}

/**
 * @description: 
 */
export function toArray(v: unknown): any[] {
  if (isFalsy(v)) return [];
  if (typeof v === 'object') return Array.from(v as Iterable<unknown> | ArrayLike<unknown>);
  return [v];
}

/**
 * @description: 
 */
export function toggleElem(
  v: string | number, // 
  set: unknown, // 
  multiple: boolean, // 
  atLeastOne = false, // 
): any[] | string | number {
  //   |  array/proxy
  const toReturnArray: boolean = !!multiple || (!!set && typeof set === 'object');

  //  ()
  const returnFn = (v: any[]) => (!toReturnArray ? v[0] : v);

  // (Proxy)
  const arr: any[] = toArray(set);
  const arrLen: number = arr.length;

  // 
  const i = findIndex(v, arr);

  // 
  if (i > -1) {
    // 
    if (arrLen < 2 && atLeastOne) {
      return returnFn(arr);
    }
    // 
    arr.splice(i, 1);
    return returnFn(arr);
  }

  //  / 
  if (!multiple && arrLen > 0) {
    // : 1
    return returnFn([v]);
  }

  // 
  arr.push(v);
  return returnFn(arr);
}
